/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.header {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    width: 300px;
    transition: all ease-in-out 0.5s;
    z-index: 9997;
    transition: all 0.5s;
    padding: 0 15px;
    background: #98bc98;
    overflow-y: auto;
    padding-top: 40px;
  }
  
  .header .profile img {
    margin: 15px auto;
    display: block;
    width: 120px;
    border: 8px solid #222121;
  }

  @import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');
  .header .profile h1 {
    font-size: 34px;
    margin: 0;
    padding: 0;
    font-weight: 600;
    -moz-text-align-last: center;
    text-align-last: center;
    font-family: 'Great Vibes', cursive;
  }
  
  .header .profile h1 a, .header .profile h1 a:hover {
    color: #fff;
    text-decoration: none;
  }
  .profile{
    padding-bottom: 30px;
  }
  .header .profile .social-links a {
    font-size: 18px;
    display: inline-block;
    background: #212431;
    color: #fff;
    line-height: 1;
    padding: 8px 0;
    margin-right: 4px;
    border-radius: 50%;
    text-align: center;
    width: 36px;
    height: 36px;
    transition: 0.3s;
  }
  
  .header .profile .social-links a:hover {
    background: #149ddd;
    color: #fff;
    text-decoration: none;
  }
  
  .main {
    margin-right: 300px;
  }
  
  @media (max-width: 1199px) {
    .header {
      right: -300px;
    }
    .main {
      margin-right: 0;
    }
  }
  .example{
    position:absolute !important;
    top: 0;
    }

